import React from 'react'
import blog1 from '../images/blogs/blog1.svg'
import blog2 from '../images/blogs/blog5.svg'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleRight } from '@fortawesome/free-solid-svg-icons';

function Projects() {
    return (
        <div className="work-container">
            <section className="card1 animate__animated animate__fadeInLeft" >
                <img src={blog1} alt="" />
                <div className="card-text">
                    <h1>Covid-19 tracker</h1>
                    <ul className='project-detail'>
                        <li>We can have a look at how many peoples are infected,recovered and passed due to the pandemic.</li>
                        <li>I've used API.</li>
                    </ul>

                    <a href="#." className='btn'>Check Out!</a>
                </div>
            </section>
            <hr />

            <section className="card2 animate__animated animate__fadeInRight">
                <img src={blog2} alt="" />
                <div className="card-text">
                    <h1>Snake Game</h1>
                    <ul className='project-detail'>
                        <li>It's the basic web version of the game we use to hang on too for hours</li>
                        <li>I've used HTML,CSS and Javascript.</li>
                    </ul>
                    <a href="https://techysj.github.io/SNAKEMANIA/" target='blank' className='btn'>Check Out!</a>
                </div>
            </section>
            <section className="btn-more animate__animated animate__fadeInUp ">
                <a href="https://github.com/techysj" target='blank'>Click for more  <FontAwesomeIcon icon={faAngleRight} ></FontAwesomeIcon></a>
            </section>



        </div>

    )
}

export default Projects;
