import React from 'react';
import 'animate.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faLinkedin} from '@fortawesome/free-brands-svg-icons';
import contact from '../images/phone.png'
import mail from '../images/mail.png'
import location from '../images/location.png'

function Contact() {
    return (
        <div className="main-contact-container">
            <div className="heading">
                <h3 className='animate__animated animate__fadeInUp'>Get In Touch</h3>
            </div>

            <div className="contact-Container animate__animated animate__fadeInLeft">
                <div className="left-frame">
                    {/* first field */}
                    <div className="contact-info">
                        <div className="contact-info-img">
                            <img src={contact} alt="" />
                        </div>
                        <div className="contact-info-text">
                            <a href="tel:+919958015642">(+91)-9958015642</a>
                        </div>
                    </div>
                    {/* second field */}
                    <div className="contact-info">
                        <div className="contact-info-img">
                            <img src={mail} alt="" />
                        </div>
                        <div className="contact-info-text">
                            <a href="mailto:techysj0505@gmail.com">techysj0505@gmail.com</a>
                        </div>
                    </div>
                    {/* third field */}
                    <div className="contact-info">
                        <div className="contact-info-img">
                            <img src={location} alt="" />
                        </div>
                        <div className="contact-info-text">
                            <p>Noida, Uttar Pradesh, 201304</p>
                        </div>
                    </div>

                </div>
            </div>
            <div className="social-frame animate__animated animate__fadeInUp">
                <p>connect with me</p>
                <div className="social-icons animate__animated animate__fadeInUp">
                    <a href="https://www.linkedin.com/in/shreyanshjha/" target="_blank" rel="noreferrer"><FontAwesomeIcon icon={faLinkedin} ></FontAwesomeIcon></a>
                    <a href="https://github.com/techysj" target="_blank" rel="noreferrer" > <FontAwesomeIcon icon={faGithub} ></FontAwesomeIcon></a>
                </div>

            </div>
        </div>

    )
}

export default Contact;
