import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileDownload } from '@fortawesome/free-solid-svg-icons';
// import { faVolumeUp } from '@fortawesome/free-solid-svg-icons';
import HomePageGIF from "../images/HomePageGIF.gif";
import 'animate.css';



function Homepage() {
    return (
        <div className="homepage">
            <div className="header">
                <h1 className="hero-text animate__animated animate__fadeInLeft">
                    Hey! <br /> I'm Shrey.
                </h1>
                <p className='animate__animated animate__fadeInUp'>Web Developer</p>
                <div className="buttons">
                    <a href="https://drive.google.com/file/d/1ByE1sifjOhIwYiNl9noFDvapn9i5MGQ-/view?usp=sharing"  target="_blank" rel="noreferrer">Download Resume <FontAwesomeIcon icon={faFileDownload} ></FontAwesomeIcon></a>
                    {/* <a href="/"rel="noreferrer">Here Me <FontAwesomeIcon icon={faVolumeUp}> </FontAwesomeIcon></a> */}

                </div>
                <div className="introgif">
                    <img className='introgif' src={HomePageGIF} alt="" />
                </div>

            </div>
        </div>
    )
}

export default Homepage
