import React from 'react'
import html from '../images/techstaks/html.png';
import css from '../images/techstaks/css.png';
import js from '../images/techstaks/js.png';
import python from '../images/techstaks/python.png';
import MySQL from '../images/techstaks/mysql.png';
import sass from '../images/techstaks/sass.png';
import reactjs from '../images/techstaks/react-js.png';
import bs from '../images/techstaks/bootstrap.png';

function skills() {
    return (
        <div className='techstacks'>
            <p className='animate__animated animate__fadeInLeft'>The Tech-Stack I've Worked With</p> <br />
            <div className="major animate__animated animate__fadeInBottomRight">
                

                <div className="single-tech">
                    <img src={html} alt="" />
                    <h4 className='img-title'>HTML5</h4>
                    <div className="overlay">
                        <div className="text">90%</div>
                    </div>
                </div>
                <div className="single-tech">
                    <img src={css} alt="" />
                    <h4 className='img-title'>css3</h4>
                    <div className="overlay">
                        <div className="text">80%</div>
                    </div>
                </div>
                <div className="single-tech">
                    <img src={js} alt="" />
                    <h4 className='img-title'>Javascript</h4>
                    <div className="overlay">
                        <div className="text">65%</div>
                    </div>
                </div>
                <div className="single-tech">
                    <img src={bs} alt="" />
                    <h4 className='img-title'>bootstrap</h4>
                    <div className="overlay">
                        <div className="text">80%</div>
                    </div>
                </div>

                


                <div className="single-tech">
                    <img src={sass} alt="" />
                    <h4 className='img-title'>Sass</h4>
                    <div className="overlay">
                        <div className="text">75%</div>
                    </div>
                </div> <br />
            </div>

            <p className='animate__animated animate__fadeInLeft'>The Tech-Stack I'm Familier With</p> <br />
            <div className="tocenter animate__animated animate__fadeInBottomRight">

                <div className="single-tech">
                    <img src={python} alt="" />
                    <h4 className='img-title'>Python</h4>
                    <div className="overlay">
                        <div className="text">60%</div>
                    </div>
                </div>
                <div className="single-tech">
                    <img src={reactjs} alt="" />
                    <h4 className='img-title'>React.js</h4>
                    <div className="overlay">
                        <div className="text">50%</div>
                    </div>
                </div>

                <div className="single-tech">
                    <img src={MySQL} alt="" />
                    <h4 className='img-title'>MySQL</h4>
                    <div className="overlay">
                        <div className="text">85%</div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default skills
